import MetaTags from 'react-meta-tags'
import React, { useState, forwardRef,useEffect } from "react"
import { Link } from "react-router-dom"
import SweetAlert from "react-bootstrap-sweetalert"
import {
    Button, Modal, Pagination, PaginationItem, PaginationLink, Card, CardBody,
    Col, Row, ModalHeader, ModalBody, Form, Input, ModalFooter
} from "reactstrap"
import { AvForm, AvField } from "availity-reactstrap-validation"
import { Edit } from '@material-ui/icons'
import PropTypes from "prop-types"
import { getQualifications ,addNewQualification,updateQualification,deleteQualification, apiError, getQualificationsFail} from "../../../store/actions"
import { connect } from "react-redux"
//Import Breadcrumb
import Breadcrumbs from "../../../components/Common/Breadcrumb"
import '../table.css';
import MaterialTable from 'material-table'
const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} style={{color: "primary"}}/>),
}
const tarifData = [

    { entreprise :"Manpower" ,chantier:"Villa Jolimont à Rabat", qualification:"Maçon Niv2", taux: "12,3",debut:"01/06/2022", fin: "30/05/2022" },
    { entreprise :"Manpower" ,chantier:"Villa Jolimont à Rabat", qualification:"Boiseur Niv1", taux: "8,5",debut:"12/08/2021", fin: "11/08/2022" },
    { entreprise :"Manpower" ,chantier:"Villa Jolimont à Rabat", qualification:"Boiseur Niv2", taux: "15,2",debut:"18/11/2021", fin: "10/11/2022" },
    { entreprise :"Manpower" ,chantier:"Parc Triomphe Casablanca", qualification:"Machiniste Niv1", taux: "10,2",debut:"12/08/2021", fin: "11/08/2022" },
    { entreprise :"Manpower" ,chantier:"Parc Triomphe Casablanca", qualification:"Maçon Niv1", taux: "12,3",debut:"12/08/2021", fin: "11/08/2022" },
    { entreprise :"Manpower" ,chantier:"Villa Jolimont à Rabat", qualification:"Maçon Niv2", taux: "13,6",debut:"12/08/2021", fin: "11/08/2022" },
    { entreprise :"Manpower" ,chantier:"Centre commercial Lilas", qualification:"Boiseur Niv1", taux: "8,9",debut:"12/08/2021", fin: "11/08/2022" },
    { entreprise :"Manpower" ,chantier:"Parc Triomphe Casablanca", qualification:"Boiseur Niv2", taux: "10,2",debut:"25/12/2021", fin: "24/12/2022" },
    { entreprise :"Manpower" ,chantier:"Villa Jolimont à Rabat", qualification:"Maçon Niv2", taux: "12,3",debut:"25/12/2021", fin: "24/12/2022" },

]
//Import Breadcrumb

const Tarif = props => {
    const [data, setData] = useState([]);

    const [modal_edit, setmodal_edit] = React.useState(false)
    const [modal_new, setmodal_new] = React.useState(false)
    const [confirm_alert, setconfirm_alert] = useState(false)
    const [success_dlg, setsuccess_dlg] = useState(false)
    const [dynamic_title, setdynamic_title] = useState("")
    const [dynamic_description, setdynamic_description] = useState("")

    const [selectedQualification, setSelectedQualification] = useState({})

    //add
    const handleValidSubmitNewQualification = (event, values) => {
        console.log("qualification values",values)
        props.addNewQualification(values)
        window.location.reload(false)
        tog_new()
    }
//upadate
    const handleValidSubmitUpdateQualification = (event, values) => {
        let obj =values
        obj.id = selectedQualification.id
        console.log(" values",obj)
        console.log("selected qualification",selectedQualification)

        props.updateQualification(values)
        //props.getQualifications()
        // props.addNewQualification(values)
        window.location.reload(false)
        tog_edit()
    }
    const editQualification = (qualification) => {
        console.log("qualification to edit",qualification)
        // props.addNewQualification(values)
        setSelectedQualification(qualification)
        tog_edit()
    }
    const deleteQualification = (qualification) => {
        console.log("Qualification to delete",qualification)
        // props.addNewQualification(values)
        setSelectedQualification(qualification)
        setconfirm_alert(true)
    }

    function tog_new() {
        setmodal_new(!modal_new)
    }
    function tog_edit() {
        setmodal_edit(!modal_edit)
    }

    const columns = [

        { title: "Entreprise", field: "entreprise" },
        { title: "Chantier", field: "chantier"},
        { title: "Qualification", field: "qualification"},
        { title: "Taux", field: "taux"},
        { title: "Début", field: "debut"},
        { title: "Fin", field: "fin"},
    ]
    /*start useeffect*/
    useEffect(() => {
        console.log("start get",props)
        props.getQualifications()

        console.log("props qualification",props)
    }, [])

    /* end useffect */

    return (
        <React.Fragment>
            <div className="page-content">
                <MetaTags>
                    <title>Tarifs</title>
                </MetaTags>
                <div className="container-fluid">
                    <Breadcrumbs maintitle="Administration" title="Personne" breadcrumbItem="Tarifs" />
                    <Row>
                        <Col lg={12}>
                            <Card>
                                <CardBody>
                                    {/* <CardTitle className="h4">Personne Internes List </CardTitle> */}
                                    <React.Fragment>
                                        <Row>
                                            <Col sm="4">
                                                <div className="search-box me-2 mb-2 d-inline-block">
                                                </div>
                                            </Col>
                                            <Col sm="8">
                                                <div className="text-sm-end">
                                                    <Button  type="button"  color="success" className="btn-rounded mb-2 me-2"
                                                             onClick={tog_new}
                                                    >
                                                        <i className="mdi mdi-plus me-1" />{" "}
                                                        Ajout Tarif
                                                    </Button>

                                                </div>
                                            </Col>
                                        </Row>
                                        {/* start table list */}
                                        <div className="table-responsive">
                                            <MaterialTable icons={tableIcons} title="Tarifs" data={tarifData} columns={columns}
                                                           options={{columnsButton:true,actionsColumnIndex: -1, exportButton: true}}
                                                           actions={[
                                                               {
                                                                   icon: () => <Edit />,
                                                                   tooltip: 'Edit Qualification',
                                                                   iconProps: { color: "primary" },
                                                                   onClick:(event, rowData) => editQualification(rowData)
                                                               },
                                                               rowData => ({
                                                                   icon: 'delete',
                                                                   tooltip: 'Delete Qualification',
                                                                   iconProps: { color: "secondary" },
                                                                   onClick:(event, rowData) => deleteQualification(rowData)
                                                                   //  disabled: rowData.birthYear < 2000
                                                               })
                                                           ]}
                                            />
                                        </div>
                                        {/* End table list */}

                                    </React.Fragment>

                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </div>

            {/* start new Model */}
            <Modal isOpen={modal_new} role="document" autoFocus={true} centered={true}
                   className="composemodal" tabIndex="-1" toggle={tog_new}>
                <div className="modal-content">
                    <AvForm
                        className="mt-4"
                    >
                        <ModalHeader toggle={tog_new}>
                            Ajout Tarif
                        </ModalHeader>
                        <ModalBody>
                            <label className="form-label" htmlFor="name">Entreprise</label>
                            <select className="form-select">
                                <option>Choisir une entreprise</option>
                                <option>Manpower</option>
                                <option>Randstad</option>

                            </select>
                            <br/>
                            <label className="form-label" htmlFor="name">Chantier</label>
                            <select className="form-select">

                                <option>Tous les chantiers *</option>
                                <option>Villa Jolimont à Rabat</option>
                                <option>Parc Triomphe Casablanca</option>
                                <option>Centre commercial Lilas - Kenitra</option>
                            </select>
                            <br/>


                            <label className="form-label" htmlFor="name">Qualifications</label>
                            <select className="form-select">
                                <option>Choisir une qualification</option>
                                <option>Maçon Niv1</option>
                                <option>Maçon Niv2</option>
                                <option>Coffreur Niv2</option>
                                <option>Machiniste</option>
                            </select>
                            <br/>
                            <div className="row">
                                <div className="col-lg-3">
                            <label className="form-label" htmlFor="resume">Taux HN</label>{" "}
                            <input
                                className="form-control"
                                type="text" placeholder="Taux HN"
                                id="example-datetime-local-input"
                            />
                                </div>
                                <div className="col-lg-3">
                                    <label className="form-label" htmlFor="resume">Taux HS </label>{" "}
                                    <input
                                        className="form-control"
                                        type="text" placeholder="Taux HS "
                                        id="example-datetime-local-input"
                                    />
                                </div>
                                <div className="col-lg-3">
                                    <label className="form-label" htmlFor="resume">Taux WE </label>{" "}
                                    <input
                                        className="form-control"
                                        type="text" placeholder="Taux WE "
                                        id="example-datetime-local-input"
                                    />
                                </div>
                                <div className="col-lg-3">
                                    <label className="form-label" htmlFor="resume">Taux Férié</label>{" "}
                                    <input
                                        className="form-control"
                                        type="text" placeholder="Taux Férié"
                                        id="example-datetime-local-input"
                                    />
                                </div>
                            </div>
                            <br/>
                            <label className="form-label" htmlFor="resume">Date début</label>{" "}
                            <input
                                className="form-control"
                                type="date"

                                id="example-datetime-local-input"
                            />
                            <br/>
                            <label className="form-label" htmlFor="resume">Date fin</label>{" "}
                            <input
                                className="form-control"
                                type="date"

                                id="example-datetime-local-input"
                            />


                        </ModalBody>
                        <ModalFooter>
                            <Button coloe="secondary" type="button" onClick={tog_new}  >Fermer</Button>
                            <Button type="submit" onClick={tog_new}  color="primary" >Enregistrer <i className="fab fa-save ms-1"></i></Button>
                        </ModalFooter>
                    </AvForm>
                </div>
            </Modal>
            {/* End new Model */}


            {/* EDIT model */}
            <Modal isOpen={modal_edit}toggle={() => { tog_edit() }}>
                <div className="modal-header">
                    <h5 className="modal-title mt-0" id="myModalLabel">
                        Modifier Tarif
                    </h5>
                    <button type="button" onClick={() => { setmodal_edit(false)  }} className="close"  data-dismiss="modal"  aria-label="Close" >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <AvForm
                    className="mt-4"
                >

                    <ModalBody>
                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Entreprise :</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control" value={"Manpower"} disabled="true" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Chantier:</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control" value={"CH001 - Villa Jolimont à Rabat"} disabled="true" />
                            </div>
                        </div>
                        {/*    <Input type="text" className="form-control" placeholder="Name" /> */}
                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Qualification :</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control" value={"Maçon Niv2"} disabled="true" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Taux :</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control" value={"15"} disabled="true" />
                            </div>
                        </div>

                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Date Début :</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control " value={"15/09/2022"} disabled="true" />
                            </div>
                        </div>

                        <div className="row">
                            <div className="mb-3 col-lg-4">
                                <label className="form-label inp" htmlFor="email">Date Fin :</label>
                            </div>
                            <div className="mb-3 col-lg-6">
                                <input type="text"  className="form-control" value={"30/12/2022"} disabled="true" />
                            </div>
                        </div>



                    </ModalBody>

                    <ModalFooter>

                        <button type="button" onClick={() => { tog_edit() }} className="btn btn-secondary waves-effect" data-dismiss="modal" >
                            Fermer
                        </button>
                        <button type="submit" onClick={() => { tog_edit() }} className="btn btn-primary waves-effect waves-light" >
                            Enregistrer
                        </button>

                    </ModalFooter>
                </AvForm>


            </Modal>
            {/* END EDIT MODEL */}


            {/* Alert Delete */}
            {confirm_alert ? (
                <SweetAlert
                    title="Are you sure?"
                    warning
                    showCancel
                    confirmButtonText="Yes, delete it!"
                    confirmBtnBsStyle="success"
                    cancelBtnBsStyle="danger"
                    onConfirm={() => {
                        console.log("selected qualification to dlete",selectedQualification)
                        props.deleteQualification(selectedQualification)
                        setconfirm_alert(false)
                        setsuccess_dlg(true)
                        setdynamic_title("Deleted")
                        setdynamic_description("Your qualifications has been deleted.")
                        window.location.reload(false)

                    }}
                    onCancel={() => setconfirm_alert(false)}
                >
                    You won't be able to revert this!
                </SweetAlert>
            ) : null}
            {/* End Alert Delete */}
        </React.Fragment>
    )
}


Tarif.propTypes =  {
    getQualifications: PropTypes.func,
    getQualificationsSuccess: PropTypes.func,
    addNewQualification: PropTypes.func,
    updateQualification: PropTypes.func,

    getQualificationsFail: PropTypes.any,
    qualifications: PropTypes.any,
    deleteQualification:PropTypes.func
}

const mapStatetoProps = state => {
    const { qualifications } = state.Qualification
    return { qualifications}
}
const mapDispatchToProps = (dispatch) => ({
    getQualifications: () => dispatch(getQualifications()),
    addNewQualification: (qualification) => dispatch(addNewQualification(qualification)),
    updateQualification: (val) => dispatch(updateQualification(val)),
    getQualificationsSuccess: () => dispatch(getQualificationsSuccess()),
    getQualificationsFail: () => dispatch(getQualificationsFail()),
    deleteQualification: (val) => dispatch(deleteQualification(val)),
});
export default connect(mapStatetoProps,mapDispatchToProps)(Tarif)
